import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsProgressCircle } from '@cds/core/progress-circle';

@Directive({ selector: 'cds-progress-circle' })
export class CdsProgressCircleDirective {
  protected element: CdsProgressCircle;

  get status() {
    return this.element.status;
  }
  @Input() set status(value) {
    this.element.status = value;
  };
  get inverse() {
    return this.element.inverse;
  }
  @Input() set inverse(value: boolean | '') {
    this.element.inverse = value === '' ? true : value;
  };
  get value() {
    return this.element.value;
  }
  @Input() set value(value) {
    this.element.value = value;
  };
  get line() {
    return this.element.line;
  }
  @Input() set line(value) {
    this.element.line = value;
  };
  get size() {
    return this.element.size;
  }
  @Input() set size(value) {
    this.element.size = value;
  };
  get i18n() {
    return this.element.i18n;
  }
  @Input() set i18n(value) {
    this.element.i18n = value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
